              <h1>Q1. <somenone>What is DNS of a Domain?</somenone></h1>
              <br>
              <h1>Ans.: The Domain Name System (DNS) is the phonebook of the Internet. Humans access information online through domain names, like nytimes.com or espn.com. Web browsers interact through Internet Protocol (IP) addresses. DNS translates domain names to IP addresses so browsers can load Internet resources. The process of DNS resolution involves converting a hostname (such as www.example.com) into a computer-friendly IP address (such as 192.168.1.1). An IP address is given to each device on the Internet, and that address is necessary to find the appropriate Internet device - like a street address is used to find a particular home.</h1>
              <br>

<h1>Q2. <somenone>What is domain provider?</somenone></h1>
